<template>
  <div id="user-login">
    <mt-header fixed title="" id="login-mint-header"></mt-header>
    <div class="logo"><img src="./../assets/images/logo.png"/></div>

    <div class="from">
      <el-row>
        <el-col :span="18" class="login-el-col">
          <div class="codeIco">+86</div>
          <el-input  v-model="phone" id="UserPhone" placeholder="" class="el-input"></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="18" class="login-el-col">
          <el-input  v-model="password" id="UserPsd" type="password" placeholder="" ></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="18" class="login-el-col">
          <el-button id="login" @click="check"  style="width:100%" type="primary"></el-button>
        </el-col>
      </el-row>
    </div>

    <div class="userForgetWrd">
      <router-link to="ForgetPassword" ><span class="ToPassword"></span></router-link>
      <router-link to="UserRegister" ><span class="ToRegister"></span></router-link>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  export default {
    name: 'user-login',
    data () {
      return{
        phone:'',
        password:''
      }
    },
    methods : {
      check:function(){
        //
        var phone = this.phone;
        var password = this.password;
        var reg=11 && /^((13|15|17|18)[0-9]{1}\d{8})$/;
        if(phone==''){
          alert("");
          return false;
        }else if(!reg.test(this.phone)){
          alert("");
          return false;
        }
        if (password == ''){
          alert('');
          return false;
        }
        //this.$router.go(-1);
        var that=this;
        axios.post("/api/user/userLogin.dnl",{
          phone: this.phone,
          password: this.password
        }).then((data)=>{
          if(data.data.msg==""){
            // tokken
             that.$store.commit('changetoken',data.data.data)
              if(that.$route.query.redirect){
                  that.$router.push({ path: that.$route.query.redirect })
              }else{
                  that.$router.push({ path: '/UserCenter' })
              }
          }
          if(data.data.code==20001){
            alert(data.data.msg)
          }
        })
        
      }
    }
  }
</script>

<!--  "scoped"  -->
<style>
  /*  */
  #login-mint-header{background-color: #FF414F;color: #fff;}

   /* logo */
  .logo{position: relative;top:40px;width: 100%;text-align: center;padding: 0.6rem 0;}
  .logo img{width: 2rem;}

   /* from */
  .from{margin-top: 1rem;}
  .userForgetWrd{width: 75%;margin: 0 auto;float: inherit;}
  .login-el-col{float: none;margin: 0 auto 0.3rem auto; position: relative}
   
  /* mint */
  .el-input{display: block}

  /* input */
  #UserPhone{background: #F7F4F8;border: none;color: #b7b5b5;text-indent: 50px;}
  #UserPsd{background: #F7F4F8;border: none;color: #b7b5b5;}
  .codeIco{position: absolute;background-size: 100%;width: 1rem;height: 100%;display: block;z-index: 9;top: 0%;left: 0%;font-size: 0.25rem;text-align: center;line-height: 40px;background: #FF414F;border-radius: 4px 0 0 4px;color: #fff;}
  
  /* button */
  #login{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}
   
   /*  */
  .userForgetWrd .ToPassword{font-family: PingFangSC-Regular;cursor: pointer;font-size: 0.25rem;color: #FF817E;text-align: center;vertical-align: text-top;font-weight: 600;}
  .userForgetWrd .ToRegister{float: right;font-size: 0.25rem;color: #928E8D;vertical-align: text-top;cursor: pointer;font-weight: 600;}

</style>



