<template>
  <div id="user-login">
    <mt-header title="" id="registerHerder">
      <router-link to="/UserLogin" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="registerFrom">

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPhone" v-model="phone" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'') " ng-pattern="/[^a-zA-Z]/" maxlength=11></el-input>
        </el-col>
      </el-row>

      <!--  -->
       <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerUserCode" v-model="zfpsword" placeholder="" ></el-input>
        </el-col>
      </el-row> 

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPassword" v-model="password" type="password" placeholder=""></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPwd" v-model="urepwd" type="password" placeholder=""></el-input>
        </el-col>
      </el-row>

      <h5 class="tbTxt"></h5>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input  id="nameTxt"  type="text" placeholder="" v-model="userName" :attr="{ minlength:1,maxlength:4 }"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="baxTxt"  type="text" placeholder="" v-model="bankCard" :attr="{maxlength:19 }"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="baxNameTxt"  v-model="trueName" type="text" placeholder=""></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-button id="registerLogin" @click="UserRegister"  style="width:100%" type="primary"></el-button>
        </el-col>
      </el-row>

      
      <router-link to="UserLogin" class="ToLogin"><span></span></router-link>

    </div>

  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  import axios from 'axios';
  export default {
    name: 'user-login',
    data () {
      return{
        phone:'', //
        password:'',//
        urepwd:'',//
        zfpsword:'', //

        userName:'',  //
        bankCard:'',  //
        trueName:'', //
        nameflag:0,   //flag
        //code:'',
        //show: true,
       ///count: '',
        //timer: null
      }
    },
    methods : {
      UserRegister:function(){
        //
        var phone = this.phone,
            password = this.password,
            urepwd = this.urepwd,
            zfpsword = this.zfpsword,
            userName = this.userName,  //
            bankCard = this.bankCard,  //
            trueName = this.trueName, //
            //UserCode = this.UserCode,
            reg= /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
            this.userName = this.userName.replace(/(^\s+)|(\s+$)/g, "");
        if(phone==''){
          alert("");
          return false;
        }else if(!reg.test(this.phone)){
          alert("");
          return false;
        }
        if (zfpsword == ''){
          alert('');
          return false;
        }
        if (password == ''){
          alert('');
          return false;
        }
        if (password != urepwd) {
          alert('');
          return false;
        }
        if(!this.userName.match(/^[\u4e00-\u9fa5]{2,4}$/)){
          this.nameflag=0;
          alert("2-4")
          // this.tipTxt="2-4";
          // this.tipBox();
          return false;
        }


        axios.post("/api/user/save.dnl",{
          phone: this.phone, //
          password: this.password, //
          zfpsword: this.zfpsword,  //

          userName:this.userName,  //
          bankCard:this.bankCard,  //
          trueName:this.trueName  //

        }).then((data)=>{
          //alert(1)
          console.log(data)
          //console.log(this.phone)
          //console.log(data.data.phone)
          if(data.data.code == 10000){
            alert(data.data.msg)
            location.href = 'UserLogin'  
            return true
          }else{
            //alert(111)
            alert(data.data.msg)
            //location.href = 'UserLogin' 
            return false
          }
        })
      },
      // 
      // getCode(){
      //   const time_count= 10;
      //   const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
      //   if(this.phone==''){
      //     alert("");
      //     return false;
      //   }else if(!reg.test(this.phone)){
      //     alert("");
      //     return false;
      //   }
      //   if (!this.timer) {
      //     this.count = time_count;
      //     this.show = false;
      //     this.timer = setInterval(() => {
      //       if (this.count > 0 && this.count <= time_count) {
      //         this.count--;
      //       } else {
      //         this.show = true;
      //         clearInterval(this.timer);
      //         this.timer = null;
      //       }
      //     }, 1000)
      //   }
      // },

    }
  }
</script>

<!--  "scoped"  -->
<style>

  /*  */
  #registerHerder{background-color: #FF414F;color: #fff;}

  /* from */
  .registerFrom{margin-top: 1rem;}
  .el-input{display: block}
  .register-el-col{float: none;margin: 0 auto 0.3rem auto; position: relative}

  /* input */
  #registerPhone,#registerPassword,#registerPwd,#nameTxt,#baxTxt,#baxNameTxt{background: #F7F4F8;border: none;color: #b7b5b5;}
  #registerUserCode{background: #F7F4F8;border: none;color: #b7b5b5;}

  /*  */
  .registerCount{position: absolute;top: 0;height: 100%;right: 0;width: 42%;background: rgba(255,65,79,1);padding: 0 8px;color: #fff;}

   /* button */
  #registerLogin{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}

  /*  */
  .ToLogin{color: rgba(255,129,126,1);font-size: 0.28rem;text-align: center;display: inline-block;width: 100%;font-weight: 600;}

  .tbTxt{font-size: 0.26rem;text-align: center;margin-bottom: 0.3rem;}
</style>



