<template>
  <div id="UserCenter">
    <!--  -->
    <tabbar :selected="selected"></tabbar>

    <!--  -->
    <header class="UserHerder">
        <h5 class="UserHerderTopTxt"></h5>
        <div class="HerderCont">
          <div class="userImages"><img src="./../assets/images/userImg.png"></div>
          <router-link to="/UserLogin" class="nserName"><span>{{userInfo.nickName}}</span></router-link>
          <div class="UserBalance">
            <div>
              <p></p>
              <span>{{userInfo.balance}}</span>
            </div>
            <div></div>
            <div>
              <p></p>
              <span>{{userInfo.countPriceMoney}}</span>
            </div>
          </div>
        </div>
    </header>

    <div class="notesList">
      <mt-cell title="" is-link to="/UserRecharge" class="listText">
        <img slot="icon" src="./../assets/images/ico_cz.png" class="listIcon">
      </mt-cell>
      <mt-cell title="" is-link to="/UserEmbody" class="listText">
        <img slot="icon" src="./../assets/images/ico_tx.png" class="listIcon">
      </mt-cell>
      <mt-cell title="" is-link  class="listText">
        <img slot="icon" src="./../assets/images/icon_yaoqing@2x.png" class="listIcon">
      </mt-cell>
      <mt-cell title="" is-link to="/CustomService" class="listText">
        <img slot="icon" src="./../assets/images/icon_kefu @2x.png" class="listIcon">
      </mt-cell>
      <mt-cell title="" is-link to="/AboutIntroduce" class="listText">
        <img slot="icon" src="./../assets/images/icon_guanyu @2x.png" class="listIcon">
      </mt-cell>
      <div  @click="outLogin">
      <mt-cell title="" class="logOff"></mt-cell>
      </div>
    </div>
    <div class="bj"></div>
 </div>
</template>

<script>
import tabbar from './common/UserTabbar';
import axios from 'axios';
export default {
    name: "UserCenter",
    data(){
        return{
            selected:"UserCenter",  //
            userInfo:{
              nickName:"",
              countPriceMoney:"",
              balance:""
            }
        }
    },
    created(){
      var that=this;
      //private BigDecimal pricePond
      axios.post("/api/user/getUserPersonal")
      .then((data)=>{
         console.log(data.data);
         if(data.data.code=="10000"){
            that.userInfo=data.data.data;
         }
        //return data.json
        
        //this.PrizePool = item.data.slice(0,1)
      })

     
    },
    components:{
      tabbar
    },methods : {
      outLogin:function () {
        console.log("11")
        this.$store.commit('removetoken')
        this.$router.push('/');
      },
    }
  }
</script>

<style>
  @media screen and (max-width: 320px) {
    #UserCenter{width: 100%; height: auto; position: static; background: #f6f6f6;}
  }
  @media screen and (min-width: 330px) {
    #UserCenter{width: 100%; height: 100%; position: fixed; background: #f6f6f6;}
  }
  /*  */
  .UserHerder{height: 3.6rem;background:url(./../assets/images/my-bj.png) no-repeat; background-size: 100%; padding-top: 0.4rem;text-align: center;}
  .UserHerderTopTxt{color: #fff;font-size: 0.3rem; margin-bottom: 0.6rem;}
  .HerderCont{width: 94%;height: 3.4rem;background: url(./../assets/images/myUser.png)no-repeat;background-size: 100%;margin: 1rem auto 0 auto;position: relative;z-index: 99;}
  .userImages{width: 1.3rem; height: 1.3rem;overflow: hidden; border-radius: 100%;border:2px solid #fff;position: absolute;left: 50%; margin-left: -0.65rem;top: -0.5rem;}
  .userImages img{width: 100%;}
  .nserName{color: #fff; font-size: 0.25rem;display: inline-block; margin-top: 1.2rem;}
  .UserBalance{font-size: 0.25rem; width: 80%;margin: 0.25rem auto 0 auto;height: 0.8rem;}
  .UserBalance div{display: inline-block; font-size: 0.25rem; color: #fff;}
  .UserBalance div:nth-child(2){margin: 0 0.62rem;border-right: 1px solid #fff;height: 0.5rem; margin-top: 0.15rem}

  /*  */
  .notesList{margin-top: 1rem;}
  .listIcon{width: 18px; height: 18px;margin-right: 10px;}
  .listText{color: #4a4a4a;}
  .mint-cell-allow-right::after {border: solid 1px #c8c8cd;border-bottom-width: 0;border-left-width: 0;width: 6px;height: 6px;}
  .mint-cell:nth-child(4){margin-top: 10px;}
  .mint-cell:nth-child(5){border-bottom: 10px solid #f6f6f6;}
  .mint-cell-wrapper {font-size: 0.25rem;background-image: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), color-stop(50%, #f6f6f6), color-stop(50%, transparent));background-image: linear-gradient(180deg, #f6f6f6, #f6f6f6 50%, transparent 50%);padding: 0 0px;width: 100%; text-indent: 10px;}
  .logOff{margin-bottom: 1.1rem;border-bottom: 10px solid #f6f6f6; text-align: center}

</style>



