
import Vue from 'vue'
import Router from 'vue-router'

import UserLogin from './../components/UserLogin.vue' //
import UserRegister from './../components/UserRegister.vue' //
import ForgetPassword from './../components/ForgetPassword.vue' //
import UserCenter from './../components/UserCenter.vue' //
import Home from './../components/Home.vue' //
import GroupList from './../components/GroupList.vue' //
import GroupListTw from './../components/GroupListTw.vue' //
import ShoppingList from './../components/ShoppingList.vue' //
import UserEmbody from './../components/UserEmbody.vue' //
import UserRecharge from './../components/UserRecharge.vue' //
import AboutIntroduce from './../components/AboutIntroduce.vue' //
import CustomService from './../components/CustomService.vue' //
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '-',
      }

    },
    {
      path: '/UserRegister',
      name: 'UserRegister',
      component: UserRegister,
      meta: {
        title: '-',
      }

    },
    {
      path: '/UserLogin',
      name: 'UserLogin',
      component: UserLogin,
      meta: {
        title: '-'
      }
    },
    {
      path: '/ForgetPassword',
      name: 'ForgetPassword',
      component: ForgetPassword,
      meta: {
        title: '-',

      }

    },
    {
      path: '/UserCenter',
      name: 'UserCenter',
      component: UserCenter,
      meta: {
        title: '-',
        requireAuth: true,
      }

    },
    {
      path: '/GroupList',
      name: 'GroupList',
      component: GroupList,
      meta: {
        title: '-',
      }

    },
    {
      path: '/GroupListTw',
      name: 'GroupListTw',
      component: GroupListTw,
      meta: {
        title: '-',
      }

    },
    {
      path: '/ShoppingList',
      name: 'ShoppingList',
      component: ShoppingList,
      meta: {
        title: '-',
      }

    },
    {
      path: '/UserEmbody',
      name: 'UserEmbody',
      component: UserEmbody,
      meta: {
        title: '-',
      }

    },
    {
      path: '/UserRecharge',
      name: 'UserRecharge',
      component: UserRecharge,
      meta: {
        title: '-',
      }

    },
    {
      path: '/AboutIntroduce',
      name: 'AboutIntroduce',
      component: AboutIntroduce,
      meta: {
        title: '-',
      }

    },
    {
      path: '/CustomService',
      name: 'CustomService',
      component: CustomService,
      meta: {
        title: '-',
      }

    },
  ],
  mode: "history"//#
})




