<template>
  <div id="userTab">
  <mt-tabbar v-model="message" fixed>

    <mt-tab-item id="MainPage">
      <span class="ico_sy ico_com"></span>
      
    </mt-tab-item>

    <mt-tab-item id="ShoppingList">
      <span class="ico_wf ico_com"></span>
      
    </mt-tab-item>

    <mt-tab-item id="GroupList">
      <span class="ico_tz ico_com"></span>
      
    </mt-tab-item>

    <mt-tab-item id="UserCenter">
      <span class="ico_my ico_com"></span>
      
    </mt-tab-item>

  </mt-tabbar>
  </div>
</template>

<script>

  export default {
    name: "userTab",
    data(){
      return{
        //tabbarmessageselected
        message:this.selected,
        //icon
        atabs:this.tabs
      }
    },
    props:{
      selected: String,
      tabs:Array,
    },

    watch: {
      message: function (val, oldVal) {
        //  val 
        switch(val){
          case 'MainPage':
            this.$router.push('/');
            break;
          case 'ShoppingList':
            this.$router.push('/ShoppingList');
            break;
          case 'GroupList':
          console.log(this.$store.state.lotteryList)
            if(this.$store.state.lotteryList.length>0){
              console.log("1")
                this.$router.push('/GroupListTw');
            }else{
              console.log("2")
                this.$router.push('/GroupList');
            }
            
            break;
          case 'UserCenter':
            this.$router.push('/UserCenter');
            break;
        }

      }

    },

  }

</script>
<style>
  .ico_com{display:block; width: 0.4rem; height: 0.4rem; margin: 0 auto 0.1rem auto;  }
  .ico_sy{ background:url(./../../assets/images/icon/icon_shouye@2x.png)no-repeat; background-size: 100%;}
  .ico_wf{ background:url(./../../assets/images/icon/icon_wanfa@2x.png)no-repeat; background-size: 100%;}
  .ico_tz{ background:url(./../../assets/images/icon/icon_touzhu@2x.png)no-repeat; background-size: 100%;}
  .ico_my{ background:url(./../../assets/images/icon/icon_wode@2x.png)no-repeat; background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected {background:none;color: #ff414f;cursor: pointer;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_sy{background:url(./../../assets/images/icon/icon_shouye_active@2x.png)no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_wf{background:url(./../../assets/images/icon/icon_wanfa_active@2x.png)no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_tz{background:url(./../../assets/images/icon/icon_touzhu_active@2x.png)no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_my{background:url(./../../assets/images/icon/icon_touzhu_active@2x.png)no-repeat;background-size: 100%;}

</style>



