import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

const store = new Vuex.Store({
  state:{
    lotteryList:[],
    tokken:"",
    userInfo:{}
  },
  mutations:{
    //()+
    //1
    changetoken(state,token) {
      //console.log(state)//state
      console.log(state,token)
      localStorage.setItem('token',token);
      state.token = token;
    },
    changeuserinfo(state,userInfo,type) {
      //console.log(state)//state
      console.log(type)
      console.log(state,userInfo)
      // localStorage.setItem('token',token);
      // state.token = token;
      state.userInfo = userInfo;
    },
    removetoken(state){
      localStorage.removeItem('token');
      localStorage.removeItem('userInfo');
      state.token = null;
      state.userInfo={};
    },
    removeLotteryList(state,idx){

      state.lotteryList.splice(idx, 1);
    },
    addLotteryList(state,map){
      state.lotteryList[0]=map;
    },
    clearLotteryList(state){
      state.lotteryList=[];
    }
  },
  actions:{
   /* increment({commit}){
      commit("INCREMENT")
    }*/
  }
})

export default store


