<template>
    <div class="ForgetPassword">

      <mt-header title="" id="forgetHerder">
        <router-link to="/UserLogin" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>

      <div class="forgectFrom">
        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="ForgetPhone" v-model="phone" placeholder="" class="el-input tico"></el-input>
          </el-col>
         </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="ForgetUserCode" v-model="UserCode" placeholder="" ></el-input>
            <mt-button type="default" size="small" v-show="show" @click="getCode" class="forgetCount"></mt-button>
            <mt-button type="primary" size="small" v-show="!show" class="forgetCount">{{count}} s</mt-button>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="newPsd" v-model="password" type="password" placeholder=""></el-input>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="repeatNewPsd" v-model="urepwd" type="password" placeholder=""></el-input>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-button id="forgetLogin" @click="UserRegister"  style="width:100%" type="primary"></el-button>
          </el-col>
        </el-row>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ForgetPassword",
        data () {
          return{
            phone:'', //
            password:'',//
            urepwd:'',//
            UserCode:'',
            code:'',
            show: true,
            count: '',
            timer: null
          }
        },
      methods : {
        UserRegister:function(){
          //
          var phone = this.phone,
            password = this.password,
            urepwd = this.urepwd,
            UserCode = this.UserCode,
            reg= /^1[3|4|5|7|8][0-9]\d{8}$/;
          if(phone==''){
            alert("");
            return false;
          }else if(!reg.test(this.phone)){
            alert("");
            return false;
          }
          if (UserCode == ''){
            alert('');
            return false;
          }
          if (password == ''){
            alert('');
            return false;
          }
          if (password != urepwd) {
            alert('');
            return false;
          }
          //
          alert("");
          location.href = 'UserLogin'
          //window.location.href='UserLogin'

        },
        // 
        getCode(){
          const time_count= 10;
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
          if(this.phone==''){
            alert("");
            return false;
          }else if(!reg.test(this.phone)){
            alert("");
            return false;
          }
          if (!this.timer) {
            this.count = time_count;
            this.show = false;
            this.timer = setInterval(() => {
              if (this.count > 0 && this.count <= time_count) {
                this.count--;
              } else {
                this.show = true;
                clearInterval(this.timer);
                this.timer = null;
              }
            }, 1000)
          }
        },

      }
    }

</script>

<style>

  /*  */
  #forgetHerder{background-color: #FF414F;color: #fff;}

  /* from */
  .forgectFrom{margin-top: 1rem;}
  .forget-el-col{float: none;margin: 0 auto 0.3rem auto; position: relative}
  .el-input {display: block;}
  
  /* input */
  #ForgetPhone,#newPsd,#repeatNewPsd{background: #F7F4F8;border: none;color: #b7b5b5;}
  #ForgetUserCode{width: 46%;background: #F7F4F8;border: none;color: #b7b5b5;}

  /* button */
  #forgetLogin{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}

  /*  */
  .forgetCount{position: absolute;top: 0;height: 100%;right: 0;width: 42%; background: rgba(255,65,79,1) ;padding: 0 8px; color: #fff;}

  
</style>



