<template>
    <div id="GroupList">
        <!--  -->
        <!-- <mt-header fixed title="" id="playheight"></mt-header> -->
        <mt-header title="" id="playheight">
        </mt-header>

        <!--  -->
        <div class="headder">
            <button class="remove" @click='addHaoMa()'><span class="addIcon addImg" ></span></button>
            <button class="remove" @click='clearList()'><span class="addIcon clearImg"></span></button>
        </div>
        <div class="list">
            <div class="item" v-for='(item,index) in list' :key="index">
                <button class="removeIco" @click='remove(index)'><i class="delIcon delImg" ></i></button>
                <div class="zNumber">1 2</div>
                <span>{{item.bai}}</span><span>{{item.shi}}</span><span>{{item.ge}}</span>         
            </div>
        </div>
        <div class="bottom">
            <div class="botTxt">&nbsp;&nbsp;<input type="text" class="iptxt" v-model="bei" @blur="getMoney()">&nbsp;&nbsp;</div>
            <div class="botBtn">
                <div class="botLeft">
                    <div>&nbsp;{{allMoney}}&nbsp;</div>
                    <div>{{list.length}}{{bei}}</div>
                </div>
                <button class="save" @click='save()'></button>
            </div>
        </div> 
    </div>
</template>

<script>
    import axios from 'axios';
    import tabbar from './common/UserTabbar';
    export default {
        name: "GroupList",
        data(){
            return{
                selected:"GroupList",  //
                bei:1,
                allMoney:0,
                list:[
                    
                ]
                
            }
        },
        components:{
            tabbar
        },
        methods: {
            getMoney:function(){
                    this.allMoney=2*this.list.length*this.bei
            },
           
            remove(idx){
                console.log(idx);
                this.$store.commit('removeLotteryList',idx);
                this.getMoney();
            },
            addHaoMa(){
                this.$router.push('/GroupList');
            },clearList(){
                this.$store.commit('clearLotteryList')
                this.list=[];
                this.bei=1;
                this.getMoney();
            },save(){
                //alert(""+this.list.length+""+this.bei+" ")
                console.log(""+this.bei);
                console.log("");
                console.log(this.list);
                if(this.list.length>0){
                    var data={
                         lotterNumber:this.list[0].bai+''+this.list[0].shi+''+this.list[0].ge,
                         multiple:this.bei
                    }
                    axios.post("/api/order/userSaveOrder",{
                        lotterNumber:data.lotterNumber,
                        multiple:data.multiple
                    }).then((data)=>{
                        //console.log(data.data.code)
                        if(data.data.code == 20005){ 
                            alert(data.data.msg)
                        }    
                    })
                }else{
                    alert("!")
                     this.$router.push('/GroupList');
                }
                
            }

        },mounted: function () {
            console.log(this.$store.state.lotteryList)
            this.list=this.$store.state.lotteryList;
            this.getMoney();
        }
    }

</script>
<style>
*{font-size: 0.25rem;}

#playheight{color: #2d2d2d;background: #fff;}
.numericalSelection{margin-top: 55px;}
.SelectionTitle{width: 76px;;height: 26px;background: #fff;font-size: 0.25rem; line-height: 26px;margin-left: 15px;font-size: 14px;color: #4A4A4A;font-weight: 500;text-indent: 8px;}
.SelectionText{font-size: 12px;text-indent: 15px;padding-top: 10px; color: #777777;}
.selectTit h4{width: 54px;font-size: 12px;color: #777777; text-indent: 10px;height: 24px;line-height: 24px; background: url(./../assets/images/titleImg.png) no-repeat;background-size: 100% 100%;}
.selectBox{margin-top: 15px;border-bottom: 1px solid #DDDDDD;}
.selectBox:last-child{border-bottom: none;margin-bottom: 50px;}
.active {color: #fff;background: #FF414F;border: 0;}
.selectTit{display: inline-block;width: 18%;vertical-align: top;padding-left: 4%;}
.selectBtn{ display: inline-block;width: 70%;}
.forterBar{right: 0;bottom: 53px;left: 0;position: fixed;z-index: 1;height: 40px;width: 100%;background: #fff;}
.forterBar div{display: inline-block; height: 40px; line-height: 40px;text-align: center;}
.forterBar div:nth-child(1){ width: 14%; background: #F1F1F1;font-size: 14px;color: #777777;float: left;}
.forterBar div:nth-child(2){ width: 66%; font-size: 14px;color: #777777;float: left;}
.forterBar div:nth-child(3){ width: 18%; background: #FF414F;font-size: 16px;color: #fff;float: right;}
.colorTxt{color: #FF414F}
.collection-item {border: 1px solid #dadada;width: 34px;height: 34px;line-height: 34px;border-radius: 100%;margin-bottom: 5px;text-align: center;font-size:16px;display: inline-block;margin-left: 15px;margin-bottom: 15px;}
@media screen and (max-width: 320px) {
    #GroupList{width: 100%; height: auto; position: static; background: #f6f6f6;}
    .forterBar{bottom: 48px;}
}
@media screen and (min-width: 330px) {
    #GroupList{width: 100%; height: 100%; position: fixed; background: #f6f6f6;}
}

.headder{margin-top: 10px;}
.list{width: 90%;margin: 0 auto;}
.zNumber{position: absolute;left: 20%;bottom: 16%;}
.remove{width: 30%;height: 26px;background: #fff;outline: none;border: 0;margin: 10px 9%;display: inline-block;}
.item{background: #fff;width: 84%;margin-bottom: 10px;position: relative;padding-left: 16%;}
.item span{border: 1px solid #dadada;width: 34px;height: 34px;line-height: 34px;border-radius: 100%;text-align: center;font-size: 16px;display: inline-block;margin-left: 15px;margin-bottom: 40px;color: #FF414F;margin-top: 10px;}
.bottom{position: fixed;width: 100%;bottom: 0px;background: #fff;}
.removeIco{position: absolute;left: 3%;top: 38%;border: 0;outline: none;background: none;}
.bottom input{text-align: center;}
.addIcon{display:inline-block;margin: -5px 5px 0 0;vertical-align: middle; width:15px; height: 15px; }
.addImg{background: url(./../assets/images/icon_jiahao@2x.png)no-repeat;background-size: 100%;}
.clearImg{background: url(./../assets/images/icon_qingchu@2x.png)no-repeat;background-size: 100%;}
.delIcon{display:inline-block;margin: -5px 5px 0 5px;;vertical-align: middle; width:18px; height: 18px; }
.delImg{background: url(./../assets/images/icon_chahao@2x.png)no-repeat;background-size: 100%;}
.iptxt{background: #FFFFFF;border: 1px solid #979797;border-radius: 2px;outline:none;width: 10%;}
.botTxt{background: #F0F0F0;padding: 5px 0;text-align: center;}
.save{background: #FF414F;border: 0;color: #fff;width:15%; float: right;height: 100%;height:40px;}
.botLeft{width:84%; float: left;height: 40px;text-indent: 53%;}
</style>



