<template>
  <div id="UserRecharge">
    <!--  -->
    <mt-header title="" id="rechargeHeader">
      <router-link to="/UserCenter" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="RechargeBox">
      <h4 class="RechargeTitle"></h4>
      <!-- <div class="RechargeText"><label for=""></label><input type="number" v-model="mony" placeholder=""></div> -->
      
      <ul class="monerBoxx">
        <li v-for="dat in data" :key="dat.dat" :class="['moneyList',{active:selectId==dat.id}] "  @click="selectIdFun(dat.id)" >
           <span>{{dat.cardName}}</span>
           <span>{{dat.money}}</span>
        </li>
      </ul>
      
      <div :class="['typeItem',{active:selectType==1}] " @click="selectType=1"></div>
      <div :class="['typeItem',{active:selectType==2}] " @click="selectType=2"></div>
    </div>
    <form style='display:none;' id='formpay' name='formpay' method='post' action='https://pay.paysapi.com' >
        <input name='goodsname' id='goodsname' type='text' v-model="formData.goodsname" />
        <input name='istype' id='istype' type='text' v-model="formData.istype" />
        <input name='key' id='key' type='text' v-model="formData.key"/>
        <input name='notify_url' id='notify_url' type='text' v-model="formData.notify_url"/>
        <input name='orderid' id='orderid' type='text' v-model="formData.orderid"/>
        <input name='orderuid' id='orderuid' type='text' v-model="formData.orderuid"/>
        <input name='price' id='price' type='text' v-model="formData.price"/>
        <input name='return_url' id='return_url' type='text' v-model="formData.return_url"/>
        <input name='uid' id='uid' type='text' v-model="formData.uid"/>
        <input type='submit' id='submitdemo1'>
    </form>

    <div class="RechargeBtn"><mt-button type="danger" class="ToBtn" @click="ToBtn"></mt-button></div>
 </div>
</template>

<script>
import axios from 'axios';
export default {
    name: "UserRecharge",
    data(){
        return{
          data:[],
          categories:[],
          selectId:"",
          selectType:1,
          formData:{}
        }
    },
    created(){
      axios.post("/api/card/getAll").then((data)=>{
        //console.log(data)
        //console.log(data.data.data)
        this.data = data.data.data
        //console.log(this.data)
      })
    },
    methods : { 
      selectIdFun:function(id){
          this.selectId=id
      }, 
      ToBtn:function(){
        var that=this;
      //private BigDecimal pricePond
      if(that.selectId){
        axios.post("/api/recharge/paysApiPay",{
            cardId:that.selectId,
            type:that.selectType
        }).then((data)=>{
            console.log(data)
            if(data.data.code == 20005){ 
                alert(data.data.msg)
            }else{
                that.formData=data.data.data
                setTimeout(function(){
                  document.getElementById("formpay").submit();
                },500)
            }  
        })
      }else{
        alert("")
      }
        
      }
    },
  }
</script>

<style>
#UserRecharge{width: 100%;height: 100%;background: #f6f6f6;position: fixed;}
#rechargeHeader{color: #2d2d2d;background: #fff;}
.RechargeBox{background: #fff;}
.RechargeTitle{font-size: 0.25rem;color: #4a4a4a;font-weight: 400;text-indent: 0.2rem;padding-top: 0.4rem;}
.RechargeText{margin-top: 0.15rem;padding-bottom: 0.15rem;text-indent: 0.2rem;height: 0.8rem; line-height: 0.8rem; display: flex;}
.RechargeText label{font-size: 0.3rem; color: #363636;line-height: 0.75rem;}
.RechargeText input{height: 0.8rem;border: 0;width: 60%;font-size: 0.32rem;outline: none;text-indent: 0.2rem;}
.RechargeBtn{border-top:4px solid #f6f6f6; margin-top: 0.2rem;}
.ToBtn{width: 60%; margin-left: 20%;}
.moneyList{font-size: 0.25rem;}

.monerBoxx{display: inline-block;width: 100%;margin-top: 10px;}
.monerBoxx .moneyList{font-size: 0.25rem;height: 30px;line-height: 30px;width: 100%;padding-left: 2%;}
.monerBoxx .moneyList span:nth-child(2){text-align: center}
.active{background: #ff6600 !important;}
.typeItem{width:50%;height:50px;float:left;line-height:50px;text-align:center;background:#ddd;margin: 10px 0;font-size: 14px;}
</style>



