<template>
    <div id="ShoppingList">
        <!--  -->
        <mt-header fixed title="" id="playheight"></mt-header>
        <div class="playRule">
            <h4></h4>
            <p>10</p>
            <h4></h4>
            <p>31-63</p>
            <h4></h4>
            <table class="tabTxt">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td>9-240</td>
                </tr>
                <tr>
                    <td></td>
                    <td>2</td>
                    <td>9-240</td>
                </tr>
            </table>
            <h4></h4>
            <p></p>
        </div>
        <!--  -->
        <tabbar :selected="selected"></tabbar>
    </div>
</template>

<script>
    import tabbar from './common/UserTabbar';
    export default {
        name: "ShoppingList",
        data(){
            return{
                selected:"ShoppingList",  //
            }
        },
        components:{
            tabbar
        },
    }

</script>
<style>
#playheight{color: #2d2d2d;background: #fff;}
.playRule{width: 94%;margin: 1rem auto 0 auto;}
.playRule h4{font-size: 13px;background: url(./../assets/images/myUser_03.png) no-repeat;width: 3rem;height: 36px;background-size: 60%;line-height: 32px;color: #fff;text-indent: 7px;font-weight: 500;}
.playRule p{font-size: 13px;margin: 0 0 0.2rem 6px; font-weight: 600;}
.tabTxt{font-size: 0.25rem;border: 1px solid #ccc;margin: 0 0 0.2rem 6px;width: 95%;text-align: center;border-collapse:collapse;}
.tabTxt tr{height: 30px;line-height: 30px;}
.tabTxt tr th{font-size: 14px;border: 1px solid #ccc;}
.tabTxt tr td{font-size: 12px; color: #2d2d2d;border: 1px solid #ccc;}
table th{background:#7c7c7c;font-weight:normal;line-height:30px;font-size:14px;color:#FFF;}
table tr:nth-child(odd){background:#F4F4F4;}
table td:nth-child(even){color:#fe515e;width: 46%;line-height: 20px;}

</style>



